<template>
  <section>
      
      <Container v-if="level==0" @levelnone="level=0" @levelup="level=1" @levelup2="level=2" @levelup3="level=3" @userPageOpen="userJoin"
      @userLogin="userLogin"></Container>
      <users v-if="userPage
       == 1"  @levelZero="levelZero" />
      <detail1 v-if="level==1" @levelnone="level=0"></detail1>
      <detail2 v-if="level==2" @levelnone="level=0"></detail2>
      <detail3 v-if="level==3" @levelnone="level=0"></detail3>
      <Login v-if="userPage==2" @levelnone="level=0"></Login>
      <div><img src="./assets/footer.png" alt="푸터"></div>

      
  </section>
</template>

<script>
import Container from './components/container.vue'
import detail1 from './components/detail1.vue'
import detail2 from './components/detail2.vue'
import detail3 from './components/detail3.vue'
import users from './components/userPage.vue'
import Login from './components/Login.vue'

export default {
  name: 'App',
  data(){
    return{
      level:0,
      userPage:0,

      
    }
  },
  methods:{
    userJoin(){
      this.level = 100
      this.userPage = 1
    },
    userLogin(){
      this.level = 101
      this.userPage=2
    },
    levelZero(){
      this.level = 0
      this.userPage=0
    }
   

  },
  components: {
    Container:Container,
    detail1:detail1,
    detail2:detail2,
    detail3:detail3,
    users,
    Login,
  },
}
</script>

