<template>
    <div class="login">
      <img src="../assets/logo.png" class="userPageLogo" alt="쿠팡로고" style="cursor: pointer;" @click="levelnone()" />
        <h3>Login</h3>
        <input type="text"  v-model="email" placeholder="email"><br>
        <input type="password" v-model="password" placeholder="password"><br>
        <button @click="login">로그인</button>
        <p class="error">{{passworderro}}</p>
        <p>계정이 없다면,<router-link to="/signUp">회원가입</router-link>을 먼저 진행해주세요!</p>
    </div>
</template>

<script>
import { getAuth, signInWithEmailAndPassword } from "firebase/auth"

export default {
    name:'loginPage',
    data() {
        return {
            passworderro:'',
        }
    },
    methods: {
        login(e){
          e.preventDefault();
            //this.$router.replace('Home')
            //로그인 버튼을 눌렀을 때 Home경로 페이지로 이동
            const auth = getAuth();
            signInWithEmailAndPassword(auth, this.email, this.password)
            .then((userCredential) => {
    // Signed in
    const user = userCredential.user;
    console.log(user);
    // ...
    this.$emit('userLogin', user);
    this.$router.replace('Home');
  })
  .catch((error) => {
    const errorCode = error.code;
    const errorMessage = error.message;
    console.log(errorCode);
    console.log(errorMessage);
    if(errorCode=='auth/wrong-password'){
      this.passworderro='비밀번호를 잘못입력하셨습니다.';
    }
    if(errorCode=='auth/user-not-found'){
      this.passworderro='존재하지 않는 사용자입니다.';
    }
    if(errorCode=='auth/invalid-email'){
      this.passworderro='올바른 이메일 형식을 입력해주세요';
    }
  });
        },
        levelnone(){
      this.$emit('levelnone');
    },
    },
}
</script>

<style scoped>
  .login {
    margin-top: 40px;
  }
  input {
    margin: 10px 0;
    width: 20%;
    padding: 15px;
  }
  button {
    margin-top: 20px;
    width: 10%;
    cursor: pointer;
  }
  p {
    margin-top: 40px;
    font-size: 15px;
  }
  p a {
    text-decoration: underline;
    cursor: pointer;
  }
button{width: 380px; background:  #2172fd; border: none; height: 40px; margin: auto; display: block; color: #fff; font-size: 17px; font-weight: bold;}
button:hover{background:#619CFF;}
</style>